<template>
  <div class="login">
      <div class="logo">
          <img src="../assets/logo.jpg" alt="my login image" />
      </div>
      <!-- 手机号 -->
      <InputGroup 
        type="number" 
        v-model="phone" 
        placeholder="手机号"
        :btnTitle="btnTitle" 
        :disabled="disabled" 
        :error="errors.phone"
        @btnClick="getVerifyCode"/>
      <!-- 验证码 -->
      <InputGroup type="number" v-model="verifyCode" placeholder="验证码"
      :error="errors.code"/>
      <!-- 用户协议 -->
      <div class="login_des">
          <p>新用户登录即自动注册，表示已经同意<span>《用户服务协议》</span></p>
      </div>
      <!-- 登录按钮 -->
      <div class="login_btn">
          <button :disabled="isClick" @click="handleLogin">登录</button>
      </div>
  </div>
</template>

<script>
import InputGroup from "../components/InputGroup.vue";
export default {
    name: "login",
    data(){
        return {
            phone: "",//手机号
            verifyCode: "",//验证码
            errors: {},
            btnTitle: "获取验证码",
            disabled: false
        }
    },
    computed: {
        isClick() {
            if(!this.phone || !this.verifyCode){
                return true;
            }else{
                return false;
            }
        }
    },
    methods: {
        handleLogin(){
            //取消错误提醒
            this.errors = {};
            this.$axios.post("/api/posts/sms_back",{
                //这里无论验证码真伪都会成功，是因为视频方后台不支持多次短信验证，所以只提供phone就回返回成功信息
                phone: this.phone,
                code: this.verifyCode,//268949长期性验证码
            }).then(res => {
                // console.log(res.data);
                //检验成功，设置登录状态并跳转
                //localStorage.setItem("ele_login",true);//不存储true，存储user登录的id
                localStorage.setItem("ele_login",res.data.user._id);
                //返回主页
                this.$router.push("/");
            }).catch(err => {
                //返回错误信息
                this.errors = {
                    code: err.response.data.msg,
                }
            })
        },
        validateBtn(){
            let time = 60;
            let timer = setInterval(() => {
                if (time == 0) {
                    clearInterval(timer);
                    this.btnTitle = "获取验证码";
                    this.disabled = false;
                }else{
                    //倒计时
                    this.btnTitle = time + "s后重试";//如果没有中文的话，当前元素的高度会变化
                    this.disabled = true;
                    time--;
                }
            },1000);
        },
        getVerifyCode() {
            if(this.validatePhone()){
                //显示倒数计时
                this.validateBtn();
                //发送网络请求
                //url写全址会有跨域，所以vue.config.js中进行配置，把/api配置为全址https://xxx/xxx/api
                this.$axios.post('/api/posts/sms_send',{
                    //现在写的是网课的人购买的数据凭证，此处应该写自己的
                    // sid: "70e9281073fe5e1546b7f657cae73897",//短信网站云之讯获取的必要属性
                    // token: "d1a5431e19d2005c3286794786111d79",//同上
                    // appid: "099a1aedcf19400a9eb83d857eb7b845",//同上
                    // templateid: "522016",//同上，模板id


                    //url:"https://open.ucpaas.com/ol/sms/{function}"
                    appid: "16d0ef64d28b45359f53efd85433a81f",//应用id
                    sid: "2609b9d5062008c07369147b9bfdac11",//用户sid
                    token: "72f3c21595441bb20eafdf2a60b6f4d8",//秘钥
                    templateid: "534169",//已经获取过了，之后不会在获取
                    phone: this.phone,//输入的手机号
                }).then(res => {
                    console.log(res);
                });
            }
        },
        validatePhone() {
            //验证手机号码
            if(!this.phone){
                this.errors = {
                    phone: "手机号码不能为空"
                };
                return false;
            }else if( !/^1[345678]\d{9}$/.test(this.phone) ){
                //手机号码格式不对
                this.errors = {
                    phone: "请填写正确的手机号码"
                };
                return false;
            }else{
                this.errors = {};
                return true;
            }
        }
    },
    components: {
        InputGroup
    }
}
</script>

<style scoped>
/* box-sizing:border-box;指定padding和margin都在宽高之内绘制，相当于IE盒子的形式 */
.login {
  width: 100%;
  height: 100%;
  padding: 30px;
  box-sizing: border-box;
  background: #fff;
}

.logo {
  text-align: center;
}
.logo img {
  width: 150px;
}
.text_group,
.login_des,
.login_btn {
  margin-top: 20px;
}
.login_des {
  color: #aaa;
  line-height: 22px;
}
.login_des span {
  color: #4d90fe;
}
.login_btn button {
  width: 100%;
  height: 40px;
  background-color: #48ca38;
  border-radius: 4px;
  color: white;
  font-size: 14px;
  border: none;
  outline: none;
}
.login_btn button[disabled] {
  background-color: #8bda81;
}
</style>